import React from 'react';
import { useLocation  } from 'react-router-dom';

export const ScDetailTable0 = ({fileName, folder, volume, mixing, output, repeat, external, channel, params}) => {
  const location=useLocation();
  console.log(`ScDetailTable1location: ${location}`);
  const rowHeaders = ["", "", "", "", ""];

  return (
    <table align='center'>
      <tr text-align='center'>
        <td colSpan={5}><b></b></td>
      </tr>
      <tr>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
      {rowHeaders.map((header, index) => (
        <tr key={index}>
          <td>{header}</td>
          <td>{fileName[index]}</td>
          <td>{folder[index]}</td>
          <td>{volume[index]}</td>
          <td>{mixing[index]}%</td>
        </tr>
      ))}
      <tr>
          <td colSpan={1}></td>
          <td></td>
          <td>{output[0]}</td>
          <td>{output[1]}</td>
          <td>{output[2]}</td>
      </tr>
      <tr>
          <td colSpan={1}></td>
          <td colSpan={4}>{repeat}</td>
      </tr>
      <tr>
          <td colSpan={1}></td>
          <td colSpan={4}>{external[0]}</td>
      </tr>
      {external[0] !== "" && (
        <>
          <tr>
            <td></td>
            <td>{external[1]}</td>
            <td colSpan={3}>(1~16)</td>
          </tr>
          <tr>
            <td></td>
            <td colSpan={4}>{external[2]}</td>
          </tr>
          {external[2] !== "" && (
          <tr>
            <td></td>
            <td colSpan={3}>{external[3]}</td>
            <td>(1~99)</td>
          </tr>
          )}
        </>
      )}
      <tr>
        <td>ch</td>
        <td colSpan={4}>{channel[0]}</td>
      </tr>
      {channel[0] !== "" && (
        <>
          <tr>
            <td></td>
            <td colSpan={4}>{channel[1]}</td>
          </tr>
          <tr>
            <td></td>
            <td colSpan={4}>{params}</td>
          </tr>
        </>
      )}
    </table>
  )
};

