export const ScDetailTable3 = ({action, cm, bgm, minute}) => {
  return (
    <table align='center'>
      <tr text-align='center'>
        <td colSpan={4}><b>BGM/CM</b></td>
      </tr>
      <tr>
        <td></td>
        <td colSpan={3}>{action}</td>
      </tr>
      <tr>
        <td></td>
        <td><br />(1)</td>
        <td><br />(2)</td>
        <td><br />(3)</td>
      </tr>
      <tr>
        <td>BGM</td>
        {bgm.slice(0, 3).map((value, index) => (
        <td key={index}>{value ? '' : ''}</td>
      ))}
      </tr>
      <tr>
        <td>CM</td>
        {cm.map((value, index) => (
        <td key={index}>{value ? '' : <td></td>}</td>
      ))}
      </tr>
      <tr>
        <td></td>
        {minute.map((value, index) => (
        <td key={index}>{value ? '' : <td></td>}</td>
      ))}
      </tr>
    </table>
  )
};

