// context/FileContext.js
import { createContext, useContext, useState } from 'react';

const FileContext = createContext(); //

export const FileProvider = ({ children }) => { //children
  const [file, setFile] = useState(null);

  const setFileContext = (newFile) => { //setFilefile
    setFile(newFile);
  };

  const resetFile = () => {
    setFile(null); // 
  };

  return (
    <FileContext.Provider value={{ file, setFileContext, resetFile }}>
      {children} {/* App.js*/}
    </FileContext.Provider>
  );
};

export const useFileContext = () => useContext(FileContext); //{FileContext}filesetFileContext
