import { processBGMBand } from "../bgmBand";

export const replaceValue = (value) => {
    switch (value) {
      case '00':
        return "ONOFF";
      case '01':
        return "ON";
      case '02':
        return "OFF";
      case '03' :
        return "";
      case '04' :
        return "";    
      default:
        return value; // 
    }
};

export const generateOutput = (input) => {
    const settingValue = {
        '00': '',
        '01': '',
        '02': '',
    }[input] || '';
    return settingValue;
};

