export const getActionResult1 = (sc, i) => {
    switch (sc[i]) {
      case '01':
        return [
          `${replaceValue(sc[i + 33])}`,
          `${replaceValue(sc[i + 22433])}`,
        ];
      case '02':
        let channel = "";
        if(sc[i+34] === '00') {
          channel = `${processBGMBand(sc[i + 37])}${parseInt(sc[i + 38], 16)}`
        } else if(sc[i+34] === '01') {
          channel = "" + (sc[i+35] === '00' ? "" : sc[i+35]);  
        } else if(sc[i+34] === '02') {
          channel = sc[i+39];
        }
        return [
          "" + channel,
          "",
        ];
      case '03':
        return [`BGM/CM ${generateOutput(sc[i + 46])}`, `BGM/CM ${generateOutput(sc[i + 22446])}`];
      case '04':
        return [
          `${parseInt(sc[i + 47], 16)} ${replaceValue(sc[i + 48])}`,
          `${parseInt(sc[i + 22447], 16)} ${replaceValue(sc[i + 22448])}`,
        ];
      case '05':
        return [
          `${parseInt(sc[i + 49], 16)} ${replaceValue(sc[i + 50])}`,
          `${parseInt(sc[i + 22449], 16)} ${replaceValue(sc[i + 22450])}`,
        ];
      case '06':
        return [` ${replaceVolume(sc[i + 53])}`, ` ${replaceVolume(sc[i + 22453])}`];
      case '07':
        return [`AUX ${replaceValue(sc[i + 55])}`, `AUX ${replaceValue(sc[i + 22455])}`];
      default:
        return [sc[i], sc[i + 22400]];
    }
  };

  export const getActionResult2 = (sc, i) => {
    switch (sc[i]) {
      case '01':
        const firstElement = `${replaceValue(sc[i + 33])}`;
        const secondElement = sc[i + 448] !== undefined ? `${replaceValue(sc[i + 448])}` : "";
        return [firstElement, secondElement];
        
      case '02':
        const firstElement2 = ` ${processBGMBand(sc[i + 37])}${parseInt(sc[i + 38], 16)}`;
        const secondElement2 = "";
        return [firstElement2, secondElement2];
      
      case '03':
        const firstElement3 = `BGM/CM ${generateOutput(sc[i + 46])}`;
        const secondElement3 = `BGM/CM ${generateOutput(sc[i + 494])}`;
        return [firstElement3, secondElement3];
      
      case '04':
        const firstElement4 = `${parseInt(sc[i + 47], 16)} ${replaceValue(sc[i + 48])}`;
        const secondElement4 = sc[i + 448] !== undefined ? `${parseInt(sc[i + 448], 16)} ${replaceValue(sc[i + 496])}` : "";
        return [firstElement4, secondElement4];
      
      case '05':
        const firstElement5 = `${parseInt(sc[i + 49], 16)} ${replaceValue(sc[i + 50])}`;
        const secondElement5 = sc[i + 448] !== undefined ? `${parseInt(sc[i + 448], 16)} ${replaceValue(sc[i + 498])}` : "";
        return [firstElement5, secondElement5];
      
      case '06':
        const firstElement6 = ` ${replaceVolume(sc[i + 53])}`;
        const secondElement6 = sc[i + 448] !== undefined ? ` ${replaceVolume(sc[i + 501])}` : "";
        return [firstElement6, secondElement6];
      
      case '07':
        const firstElement7 = `AUX ${replaceValue(sc[i + 55])}`;
        const secondElement7 = sc[i + 448] !== undefined ? `AUX ${replaceValue(sc[i + 503])}` : "";
        return [firstElement7, secondElement7];
      
      default:
        return [sc[i], sc[i + 22400]];
    }
  };
  